import React, { useState } from "react";
import axios from "axios";

import "./rsvp.scss";
import Lilly from "../../images/cala-lilly.png";

const RSVP = () => {
  const [rsvpState, setRsvpState] = useState({
    answer: "",
    firstName: "",
    lastName: "",
    food: "",
    guestFirstName: "",
    guestLastName: "",
    guestFood: "",
    message: "",
  });
  const [contentState, setContentState] = useState("form");
  const [guest1Display, setGuest1Display] = useState(false)
  const [guest2Display, setGuest2Display] = useState(false)

  const formSubmitted = (event) => {
    event.preventDefault();

    const payload = {...rsvpState, timeOfSubmission: new Date()}

    axios
      .post(
        "https://my-wedding-website-701bb-default-rtdb.firebaseio.com/rsvp.json",
        payload
      )
      .then((response) => {
        console.log(response);
        setContentState("success");
      })
      .catch((error) => {
        console.log(error);
        setContentState("error");
      });
  };

  const inputChangedHandler = (event) => {
    const input = event.target.id;

    switch (input) {
      case "rsvp-yes":
        setRsvpState({
          ...rsvpState,
          answer: event.target.value,
        });
        setGuest1Display(true)
        break;
      case "rsvp-no":
        setRsvpState({
          ...rsvpState,
          answer: event.target.value,
        });
        setGuest1Display(false)
        setGuest2Display(false)
        break;
      case "firstName":
        setRsvpState({
          ...rsvpState,
          firstName: event.target.value,
        });
        break;
      case "lastName":
        setRsvpState({
          ...rsvpState,
          lastName: event.target.value,
        });
        break;
      case "veggie":
        setRsvpState({
          ...rsvpState,
          food: event.target.value,
        });
        break;
      case "meat":
        setRsvpState({
          ...rsvpState,
          food: event.target.value,
        });
        break;
      case "guest2firstName":
        setRsvpState({
          ...rsvpState,
          guestFirstName: event.target.value,
        });
        break;
      case "guest2lastName":
        setRsvpState({
          ...rsvpState,
          guestLastName: event.target.value,
        });
        break;
      case "guest2meat":
        setRsvpState({
          ...rsvpState,
          guestFood: event.target.value,
        });
        break;
      case "guest2veggie":
        setRsvpState({
          ...rsvpState,
          guestFood: event.target.value,
        });
        break;
      case "message":
        setRsvpState({
          ...rsvpState,
          message: event.target.value,
        });
        break;
      default:
        console.log("no rsvp case met")
    }
  };

  const inputChangedGuest2Handler = (event) => {
    const input = event.target.id;

    if (input === "guest2-yes") {
      setGuest2Display(true)
    } else if (input === 'guest2-no') {
      setGuest2Display(false)
    }
  };

  let formContent = (
    <form className="rsvp-form" onSubmit={formSubmitted}>
      <img src={Lilly} alt="" className="form-bkg-img" style={{opacity: '20%'}} />
      <section className="rsvp-form__radio" id="rsvpSection">
        <h2 className="rsvp-form__label--rsvpQuestion">Will you be attending?</h2>
        <div className="rsvp-form__radio-container">
          <label className="rsvp-form__radio-label" htmlFor="rsvp-yes">
            I Gladly Accept
          </label>
          <input
            className="rsvp-form__radio-input"
            type="radio"
            id="rsvp-yes"
            name="rsvp"
            value="yes"
            onChange={inputChangedHandler}
            required
          />
        </div>
        <div className="rsvp-form__radio-container">
          <label className="rsvp-form__radio-label" htmlFor="rsvp-no">
            I Regretfully Decline
          </label>
          <input
            className="rsvp-form__radio-input"
            type="radio"
            id="rsvp-no"
            name="rsvp"
            value="no"
            onChange={inputChangedHandler}
            required
          />
        </div>
        <div className="rsvp-form__firstNameLabel">
          <label className="rsvp-form__label" htmlFor="firstName">
            First Name:
          </label>
          <input
            className="rsvp-form__input"
            type="text"
            id="firstName"
            placeholder="First Name"
            onChange={inputChangedHandler}
            required
          />
        </div>
        <div>
          <label className="rsvp-form__label" htmlFor="lastName">
            Last Name:
          </label>
          <input
            className="rsvp-form__input"
            type="text"
            id="lastName"
            placeholder="Last Name"
            onChange={inputChangedHandler}
            required
          />
        </div>
      </section>
      <hr className={guest1Display ? '' : 'hide'}/>
      <section className={guest1Display ? '' : 'hide'}>
        <h2 className="rsvp-form__sectionHeader">Guest 1</h2>
        <div>
          <p className="rsvp-form__label rsvp-form__label--question">What would you like for dinner?</p>
          <div className="rsvp-form__radio-container">
            <label className="rsvp-form__radio-label" htmlFor="meat">
              Meat Entree
            </label>
            <input
              className="rsvp-form__radio-input"
              type="radio"
              id="meat"
              name="food1"
              value="meat"
              onChange={inputChangedHandler}
            />
          </div>
          <div className="rsvp-form__radio-container">
            <label className="rsvp-form__radio-label" htmlFor="rsvp-no">
              Vegetarian Entree
            </label>
            <input
              className="rsvp-form__radio-input"
              type="radio"
              id="veggie"
              name="food1"
              value="veggie"
              onChange={inputChangedHandler}
            />
          </div>
        </div>
      </section>
      <section className={guest1Display ? '' : 'hide'}>
        <p className="rsvp-form__label rsvp-form__label--question">Are you RSVP-ing another guest?</p>
        <label className="rsvp-form__radio-label" htmlFor="guest2-yes">
          Yes
        </label>
        <input
          className="rsvp-form__radio-input"
          type="radio"
          id="guest2-yes"
          name="guest2"
          value="yes"
          onChange={inputChangedGuest2Handler}
        />
        <label className="rsvp-form__radio-label" htmlFor="guest2-no">
          No
        </label>
        <input
          className="rsvp-form__radio-input"
          type="radio"
          id="guest2-no"
          name="guest2"
          value="no"
          onChange={inputChangedGuest2Handler}
        />
      </section>
      <section className={guest2Display ? '' : 'hide'}>
        <hr />
        <h2 className="rsvp-form__sectionHeader">Guest 2</h2>
        <div>
          <label className="rsvp-form__label" htmlFor="guest2firstName">
            First Name:
          </label>
          <input
            className="rsvp-form__input"
            type="text"
            id="guest2firstName"
            placeholder="First Name"
            onChange={inputChangedHandler}
          />
        </div>
        <div>
          <label className="rsvp-form__label" htmlFor="guest2lastName">
            Last Name:
          </label>
          <input
            className="rsvp-form__input"
            type="text"
            id="guest2lastName"
            placeholder="Last Name"
            onChange={inputChangedHandler}
          />
        </div>
        <div>
          <p className="rsvp-form__label rsvp-form__label--question">What would you like for dinner?</p>
          <div className="rsvp-form__radio-container">
            <label className="rsvp-form__radio-label" htmlFor="guest2meat">
              Meat Entree
            </label>
            <input
              className="rsvp-form__radio-input"
              type="radio"
              id="guest2meat"
              name="food2"
              value="meat"
              onChange={inputChangedHandler}
            />
          </div>
          <div className="rsvp-form__radio-container">
            <label className="rsvp-form__radio-label" htmlFor="guest2veggie">
              Vegetarian Entree
            </label>
            <input
              className="rsvp-form__radio-input"
              type="radio"
              id="guest2veggie"
              name="food2"
              value="veggie"
              onChange={inputChangedHandler}
            />
          </div>
        </div>
      </section>
      <hr />
      <section>
        <label htmlFor="message" className="rsvp-form__label rsvp-form__label--question">
          Would you like to leave a message for the couple?
        </label>
        <textarea id="message" onChange={inputChangedHandler} />
      </section>
      <button className="rsvp-form__btn" type="submit">
        Submit Form
      </button>
    </form>
  );

  let successContent = (
    <p className="success-message">Form Submitted Successfully</p>
  );

  let errorContent = <p className="error-message">An error occured</p>;

  let mainContent = "";

  if (contentState === "form") {
    mainContent = formContent;
  } else if (contentState === "success") {
    mainContent = successContent;
  } else if (contentState === "error") {
    mainContent = errorContent;
  }

  return (
    <div>
      <h1>RSVP</h1>
      {mainContent}
    </div>
  );
};

export default RSVP;
